.about {
  display: grid;
  grid-area: about;
  height: 80vh;
  background-color: #3B5998;
}
.photo {
  width: 150px;
  height: 150px;
  background-color: #fff;
  border-radius: 100px;
  border: 10px #7991c5 solid;
  align-self: center;
  justify-self: center;
}
h2 {
  text-transform: uppercase;
  font-size: 20px;
  font-family: Lato, serif;
  font-weight: 700;
  color: #7991c5;
  align-self: center;
  justify-self: center;
}
h3 {
  text-transform: uppercase;
  font-size: 20px;
  font-family: Lato, serif;
  font-weight: 400;
  color: #7991c5;
  align-self: center;
  justify-self: center;
}
p {
  padding: 10px 0;
  margin: 0 10px;
  border-top: 1px #7991c5 solid;
  border-bottom: 1px #7991c5 solid;
  font-size: 15px;
  font-family: Lato, serif;
  font-weight: 400;
  color: #7991c5;
  align-self: center;
  justify-self: center;
}
