.game {
  margin: 10px;
  font-family: Lato, serif;
  font-weight: 700;
  display: grid;
  font-size: 50px;
}
.game span {
  font-size: 20px;
}
.item {
  width: 100%;
  text-align: center;
  margin-top: 10px;
  justify-self: center;
  text-decoration: none;
  color: #fff;
  background-color: #577285;
}
.item:hover {
  background-color: #3B5998;
  transition: 0.5s;
}
