.navbar {
  display: grid;
  grid-area: navbar;
  height: 50vh;
  background-color: #3B5998;
  align-content: space-between;
}
.item {
  font-family: Lato, serif;
  font-weight: 700;
  text-align: center;
  height: 80px;
  text-decoration: none;
  font-size: 25px;
  color: #fff;
}
.item.active {
  background-color: #fff;
  color: #3B5998;
}
