.s {
  background: #333;
  display: grid;
}
.app {
  margin: 10vh auto;
  display: grid;
  grid-gap: 3px;
  width: 80vw;
  grid-template-areas: "navbar content about";
  grid-template-rows: 80vh;
  grid-template-columns: 1fr 8fr 3fr;
}
.app__content {
  grid-area: content;
  height: 80vh;
  background-color: #fff;
}
