.field {
  user-select: none;
  z-index: 10;
  display: grid;
  height: 100%;
  width: 100%;
  background: #696464;
  grid-template-rows: 1fr 1fr 1fr 1fr 0.5fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.field__btn {
  text-align: center;
}
.field__btn button {
  transform: translateY(50%);
  height: 50%;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 500;
  outline: none;
}
.cube {
  width: 97%;
  height: 97%;
  background-color: #888;
  justify-self: center;
  align-self: center;
  font-size: 70px;
  font-weight: 700;
  text-align: center;
  border-radius: 10px;
}
.cube__2 {
  background-color: #cfcfcf;
}
.cube__4 {
  background-color: #acacac;
}
.cube__8 {
  background-color: #e49c3f;
}
.cube__16 {
  background-color: #d47f00;
}
.cube__32 {
  background-color: #e63d3d;
}
.cube__64 {
  background-color: #ff1919;
}
.cube__128 {
  background-color: #f3e45b;
}
.cube__256 {
  background-color: #e2f10e;
}
.cube__256 {
  background-color: #facc00;
}
